import React, { Component } from 'react';
import PropTypes from 'prop-types';

//import axios from 'axios';
import {withRouter} from 'react-router-dom';

import classnames from 'classnames';
import {connect} from 'react-redux';
import {registerUser} from '../../actions/authActions';

import TextFieldGroup from '../common/TextFieldGroup';

class Register extends Component {

    constructor(){
        super();
        this.state = {
            name: '',
            email: '',
            password: '',
            password2: '',
            errors: {}
        };

        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }

    componentDidMount(){
        if(this.props.auth.isAuthenticated){
            this.props.history.push('/dashboard');
        }
    }

    // prop    
    //   componentDidMount   
    componentWillReceiveProps(nextProps){
        if(nextProps.errors){ // nextProps  ..
            this.setState({ errors: nextProps.errors }); // state errors 
        }
    }

    onChange(e) {
        this.setState({
            [e.target.name]: e.target.value
        });
    }

    onSubmit(e) {
        e.preventDefault();

        const newUser = {
            name: this.state.name,
            email: this.state.email,
            password: this.state.password,
            password2: this.state.password2
        };

        //axios 
        /*
        axios
            .post('/api/user/register', newUser) //   
            .then(res => console.log(res.data)) // 
            .catch(err => this.setState({errors: err.response.data})); // 
        */

       // ,  
       this.props.registerUser(newUser, this.props.history); // history redux dev tool   
    }

    render() {

        const {errors} = this.state;
        const {user} = this.props.auth; // props.auth user .

        return (
            <div className="register">
                {user ? user.name : null} {/** user       ( ) */}
                <div className="container">
                    <div className="row">
                        <div className="col-md-8 m-auto">
                            <h1 className="display-4 text-center">Sign Up</h1>
                            <p className="lead text-center">
                                Create your DevConnector account
                            </p>
                            <form noValidate onSubmit={this.onSubmit}>
                                <TextFieldGroup
                                    type="name"
                                    placeholder="input name"
                                    name="name"
                                    value={this.state.name}
                                    onChange={this.onChange}
                                    error={errors.name}
                                />

                                <TextFieldGroup
                                    type="email"
                                    placeholder="input email"
                                    name="email"
                                    value={this.state.email}
                                    onChange={this.onChange}
                                    error={errors.email}
                                    info="This site uses Gravatar so if you want a profile image, use a Gravatar email"
                                />

                                <TextFieldGroup
                                    type="password"
                                    placeholder="input password"
                                    name="password"
                                    value={this.state.password}
                                    onChange={this.onChange}
                                    error={errors.password}
                                />

                                <TextFieldGroup
                                    type="password"
                                    placeholder="input password2"
                                    name="password2"
                                    value={this.state.password2}
                                    onChange={this.onChange}
                                    error={errors.password2}
                                />
                                <input type="submit" className="btn btn-info btn-block mt-4" value="Sign Up" />
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
/** 
 * proptypes Proptypes  ..  import   
 *  react   
 */
Register.propTypes = { 
    registerUser: PropTypes.func.isRequired,
    auth: PropTypes.object.isRequired,
    errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
    auth: state.auth,
    errors: state.errors
});

//export default Register;

// register     
//         
//    prop 
export default connect(mapStateToProps, {registerUser})(withRouter(Register));